





































import { Component, Vue } from 'vue-property-decorator';

@Component
export default class ControlPanel extends Vue {
  private clickDelete = false;

  private deleteAll() {
    if (this.clickDelete) {
      this.$emit('delete-all');
    } else {
      this.clickDelete = true;
      setTimeout(() => {
        this.clickDelete = false;
      }, 1000);
    }
  }
}
